import { HubCrudComponentForm } from './../../utils/hub-crud-component-edit';
import { UserService, UserData } from './../user.service';
import { Component, ViewChild, ElementRef, AfterContentChecked } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ThfCheckboxGroupOption, ThfMultiselectOption } from '@totvs/thf-ui/components/thf-field';
import { ThfGridColumn } from '@totvs/thf-ui/components/thf-grid/thf-grid-column.interface';
import { HubApiQueryString } from '../../utils/hub-api';
import { OrganizationService } from '../../organizationUnit/organizationUnit.service';
import { RoleService } from '../../role/role.service';
import { TenantService } from '../../tenant/tenant.service';
import { AuthService } from '../../auth/auth.service';
import { ThfLookupColumn } from '@totvs/thf-ui/components/thf-field/thf-lookup/interfaces/thf-lookup-column.interface';
import { TenantThfLookupService } from '../../tenant/tenant.thflookup.service';

@Component({
  selector: 'app-form-user',
  templateUrl: './form-user.component.html',
  styleUrls: ['./form-user.component.css']
})

/**
 * @description
 * A classe responsável pelas ações do formulário de user
 * @extends HubCrudComponentForm
 */
export class FormUserComponent extends HubCrudComponentForm<UserData, number> implements AfterContentChecked {
  public roles: Array<ThfMultiselectOption>;
  public organizations: Array<ThfMultiselectOption>;
  public isActiveList: Array<ThfCheckboxGroupOption> = [{ value: 'isActive', label: 'Ativo' }];
  public isActiveResponse: Array<any> = [];
  public passwordReplay: string = '';
  public selectedRoles: Array<any> = [];
  public selectedOrganizations: Array<any> = [];
  private canFilterByTenant: boolean = true;
  public tenantColumns: ThfLookupColumn[] = [
    { column: 'tenantName', label: 'Nome', type: 'string' },
    { column: 'name', label: 'Nome de Exibição', type: 'string', fieldLabel: true }
  ];
  private tenantId;

  public phoneMask: string = '(99) 99999-9999';
  public emailPattern = /^([\w-\.]*)@([\w-]+)\.([a-z]{1,6}(?:\.[a-z]{1,6})?)$/;

  private _rawPassword: string;

  get rawPassword(): string {
    return this._rawPassword;
  }

  set rawPassword(newPassword: string) {
    this._rawPassword = newPassword;
    this.data.password = btoa(newPassword);
  }

  /**
   * O Contrutor para inicialização
   */
  constructor(protected activatedRoute: ActivatedRoute,
    public router: Router,
    public dataService: UserService,
    public tenantThfLookupService: TenantThfLookupService,
    public organizationService: OrganizationService,
    public roleService: RoleService,
    public tenantService: TenantService,
    public authService: AuthService) {
    super(activatedRoute, router, dataService, UserData);

    dataService.addExpand("organizations.organizationUnit");
    dataService.addExpand("roles.role");

    this.roles = [];
    this.organizations = [];

    this.beforeEditing.subscribe(data => {
      this.tenantId = data.tenantId;
      this.isActiveResponse = data.isActive ? ['isActive'] : [];
      this.beforePersisting.subscribe(this.fillRolesAndOrganizations);

      if (!authService.isHostUser()) {
        tenantService.getQuery(new HubApiQueryString({
          page: 1,
          pageSize: 1
        })).subscribe((success) => {
          if (success && success.items)
            this.data.tenantId = success.items[0].id;
        });
      }

      this.onChangedTenant();
    });
  }

  getPasswordPattern() {
    return `\\b${this.rawPassword}\\b`;
  }

  private fillRolesAndOrganizations = (data: UserData) => {
    data.organizations = this.selectedOrganizations;
    data.roles = this.selectedRoles;

    if (data.tenantId === 0)
      data.tenantId = null;
  }

  ngAfterContentChecked() {
    if (this.data.tenantId &&
        this.tenantId !== this.data.tenantId) {
      this.tenantId = this.data.tenantId;
      this.onChangedTenant();
    }
  }

  onChangedTenant() {
    if (this.canFilterByTenant) {
      this.canFilterByTenant = false;
      this.filteredOrganizationByTenantId();
      this.filteredRolesByTenantId();
    }
  }

  filteredOrganizationByTenantId() {
    this.organizations = [];

    var query = new HubApiQueryString({
      page: 1,
      pageSize: 100
    });

    this.organizationService.getQueryByTenant(query, this.data.tenantId).subscribe((success) => {
      if (success && success.items) {
        for (let org of success.items)
          this.organizations.push({ value: org.id, label: org.displayName });
      }
      this.canFilterByTenant = true;
      this.selectedOrganizations = this.data.organizations;
    });
  }

  filteredRolesByTenantId() {
    this.roles = [];

    var query = new HubApiQueryString({
      page: 1,
      pageSize: 100
    });

    this.roleService.getQueryByTenant(query, this.data.tenantId).subscribe((success) => {
      if (success && success.items) {
        for (let role of success.items)
          this.roles.push({ value: role.id, label: role.displayName });
      }
      this.canFilterByTenant = true;
      this.selectedRoles = this.data.roles;
    });
  }
}



