import { UserRoutingModule } from './user-routing.module';
import { ThfModule } from '@totvs/thf-ui/thf.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { UserComponent } from './user.component';
import { FormUserComponent } from './form-user/form-user.component';
import { UserService } from './user.service';
import { OrganizationService } from '../organizationUnit/organizationUnit.service';
import { RoleService } from '../role/role.service';
import { TenantService } from '../tenant/tenant.service';
import { AuthService } from '../auth/auth.service';
import { TokenInterceptor } from '../auth/token.interceptor';
import { TenantThfLookupService } from '../tenant/tenant.thflookup.service';

@NgModule({
  imports: [
    CommonModule,
    ThfModule,
    UserRoutingModule,
    FormsModule,
    HttpClientModule
  ],
  declarations: [
    UserComponent,
    FormUserComponent
  ],
  providers: [
    UserService,
    RoleService,
    OrganizationService,
    TenantService,
    AuthService,
    TenantThfLookupService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    }
  ]
})

/**
 * @description
 * Classe principal do modulo de user
 */
export class UserModule { }



